@import url('../node_modules/@fortawesome/fontawesome-free/css/all.min.css');
@import url('../node_modules/bulma/css/bulma.min.css');
@import url('../node_modules/animate.css/animate.min.css');

embed,
object {
  width: 210mm;
  height: 297mm;
  margin: 0 auto;
}

.no-print {
  display: block;
}

@media print {
  .no-print {
    display: none;
  }
}

@media screen and (min-width: 1024px) {
  .container {
    max-width: 1024px;
  }
}