embed,
object {
  width: 210mm;
  height: 297mm;
  margin: 0 auto;
}

.no-print {
  display: block;
}

@media print {
  .no-print {
    display: none;
  }
}

@media screen and (min-width: 1024px) {
  .container {
    max-width: 1024px;
  }
}

textarea {
  overflow-wrap: normal;
  white-space: nowrap;
}

